@import "./../../assets/mixins.scss";
@import "./../../assets/palette.scss";

.Footer {
    margin-top: 50px;
    height: 30px;
    left: 0;
    bottom: 0;
    right: 0;
    @include flex($just-cont: center);
    position: fixed;
    box-shadow: 0 0 10px $box-shadow;
    &__Link {
        font-family: $body-font;
        @include text-style(16px, 400, 27px, $decoration: underline);
        color: $black;
    }
}
